<script>
import { mapState } from 'vuex';
import MaskList from '../../components/mask';
import NavBar from "../../components/NavBar";
import footBar from "../../components/foot";
// 如果时移动端，IsMobile函数返回true
function IsMobile() {
    var userAgentInfo = navigator.userAgent;
    var Agents = ["Android", "iPhone",
                "SymbianOS", "Windows Phone",
                "iPad", "iPod"];
    var flag = false;
    for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) >= 0) {
            flag = true;
            break;
        }
    }
    return flag;
}

export default {
     components: {
        NavBar,
        MaskList,
        footBar
  },
  data(){
      return{
          friendList:[1,2,3,4],
           become:false,
        //    对应pc高，和移动英文版高
           height:"1600",
      }
  },
   computed: {
    ...mapState({
       isMask: state => state.isMask,
       isAndroid: state => state.isAndroid,
       isiOS: state => state.isiOS,
    }),

     i18n(){
          return this.$i18n.locale
      },
  },
  watch: {
    isMask: function(val) {
      const indexH = document.querySelector('.index');
      const mainH = document.documentElement.clientHeight;
      this.$nextTick(() => {
        indexH.style.height =val? mainH +'px' : 100 +'%';
        indexH.style.overflow = val? 'hidden' : 'visible';
      })
    },

     i18n(){
      this.become = !this.become  /* 控制头部英文版时的伸缩 */

      if(IsMobile()){    /* 移动端，居中的主体内容与脚部的距离时60px */
          this.$nextTick(()=>{
           var innerHight = (this.$refs.contentTgp.offsetHeight) + 60
           this.height = innerHight + ''
          })
      }else{         /* pc端，居中的主体内容与脚部的距离时200px */
            this.$nextTick(()=>{
           var innerHight = (this.$refs.contentTgp.offsetHeight) + 200
           this.height = innerHight + ''
             })
          }
      }  
  },
  mounted(){
        //  让居中主体内容与脚部的距离，在pc和移动端，切换语言时都为一个固定距离
          if(IsMobile()){                  /* 移动端，居中的主体内容与脚部的距离时60px */
          this.$nextTick(()=>{
           var innerHight = (this.$refs.contentTgp.offsetHeight) + 60
           this.height = innerHight + ''
          })
      }else{                              /* pc端，居中的主体内容与脚部的距离时200px */
            var innerHight = (this.$refs.contentTgp.offsetHeight) + 200
            this.height = innerHight + ''
      }

    //    控制头部英文版时伸缩
       if(this.$i18n.locale=='en'){
           this.become = true
      }else{
          this.become = false
      }
        window.addEventListener('resize'  , this.handerRize)
  },
  methods:{
      go(path){
          this.$router.push(path)
      },
        handerRize(){
            //  var contentTgp = document.getElementById("contentTgp")
                 if(document.body.offsetWidth < 1200 && document.body.offsetWidth > 992){
                    // this.height = ((contentTgp.offsetHeight) + 200) + ""
                    this.height = ((this.$refs.contentTgp.offsetHeight) + 200) + ""
                 }
                   if(document.body.offsetWidth > 1200){
                    // this.height = ((contentTgp.offsetHeight) + 200) + ""
                    this.height = ((this.$refs.contentTgp.offsetHeight) + 200) + ""
                 }
                  if(document.body.offsetWidth < 992 && document.body.offsetWidth > 790){
                    // this.height = ((contentTgp.offsetHeight) + 200) + ""
                    this.height = ((this.$refs.contentTgp.offsetHeight) + 200) + ""
                 }
                   if(document.body.offsetWidth <768 ){
                    // this.height = ((contentTgp.offsetHeight) + 200) + ""
                    this.height = ((this.$refs.contentTgp.offsetHeight) + 60) + ""
                 }

         }
  },
   destroyed(){
      window.removeEventListener('resize',this.handerRize)
  }
}
</script>
